* {
  box-sizing: border-box;
  font-family: "Montserrat";
}

.SearchWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.search {
  height: 10vh;
  width: 100vw;
  display: flex;
  justify-content:space-around;
  align-items: center;
  flex-direction: column;
  margin-top: 0.5rem;
  position: relative;
}

.search input {
  background-color: white;
  border:0;
  border-radius: 0.5rem;
  font-size: 1rem;
  height: 1.5rem;
  width: 220px;
  padding-left: 10px;
}

input:focus{
    outline: none;
}

.dropdown {
  display: flex;
  flex-direction: column;
  height: 1.5rem;
  width: 12rem
}

.dropdown:empty{
  border: none;
}

.dropdown-row {
  cursor: pointer;
  text-align: start;
  margin: 2px 0;
}

.dropdown-row:hover {
  color: white;
}

button{
  font-family: "Montserrat";
  border-radius: 0.5rem;
  border: none;
  height: 1.5rem;
  width: 5rem;
  background-color: #26008050;
  color: white;
  cursor: pointer;
  transition: 0.2s all;
  margin: 0 0.5rem;
}

button:hover {
  background: rgba(146, 146, 219, 0.562);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

@media screen and (max-width: 375px) {
  .search div {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  button {
    margin: 0.5rem;
  }
}